<template>
    <div>
        <van-nav-bar
            title=""
            left-text=""
            left-arrow
            @click-left="onClickLeft"
        />
        <div class="form-box">
            <van-cell-group>
                <van-field
                    v-model="username"
                    clearable
                    label=""
                    placeholder="11"
                    required
                />
                <!-- <van-field
                    v-model="sms"
                    center
                    clearable
                    type="number"
                    label=""
                    placeholder=""
                    required
                >
                    <van-button slot="button" size="small" type="default"></van-button>
                </van-field> -->
                <van-field
                    v-model="password"
                    clearable
                    type="password"
                    label=""
                    placeholder="620+"
                    required
                />
            </van-cell-group>
            
        </div>
        <div class="sub-btn">
            <van-button 
            :loading="loading" 
            :disabled="disabled" 
            @click="submit" 
            type="danger" 
            block 
            size="normal" 
            class="submit-btn" loading-text="..."></van-button>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import url from '@/serviceAPI.config'
export default {
    name:"register",
    data(){
        return {
            username:'',
            password:'',
            sms:'',
            disabled:false,
            loading:false,
        }
    },
    methods:{
        onClickLeft(){
            this.$router.back(-1)
        },
        //
        _checkForm(loadingFn){
            //
            let phone = !(/^1[34578]\d{9}$/.test(this.username))
            //
            if(phone){
                this.$toast('')
                loadingFn(this)
                return false; 
            }else if(this.password==""){
                this.$toast('')
                loadingFn(this)
                return false; 
            }else{
                return true;
            }
        },
        async submit(){
            this.disabled = true
            this.loading = true
            //
            let loadingFn = (that)=>{
                that.disabled = false
                that.loading = false
            }
            if(this._checkForm(loadingFn)){
                let res = await axios({
                    method:'post',
                    url:url.loginUser,
                    data:{
                        username:this.username,
                        password:this.password
                    }
                })
                loadingFn(this)
                if(res.status==200){
                    if(res.data.code==0){
                        this.$toast(res.data.msg)
                        localStorage.setItem('login',this.username)
                        setTimeout(()=>{
                            this.$router.push('/')
                        },500)
                    }else{
                        this.$toast(res.data.msg)
                    }
                }else{
                    this.$toast('!')
                }
            }
        }
    },
    created(){
        let checkLogin = localStorage.getItem('login')
        if(checkLogin){
            this.$toast('')
            setTimeout(()=>{},100)
            this.$router.push('/')
        }
    }
}
</script>

<style lang="less" scoped>
.form-box{
    padding:.5rem .2rem 0 .2rem;
}
.sub-btn{
    padding:.5rem;
}
</style>



