<template>
    <div>
        <van-nav-bar
            title=""
            left-text=""
            left-arrow
            @click-left="onClickLeft"
        />
        <van-address-list
            v-model="chosenAddressId"
            :list="list"
            @add="onAdd"
            @edit="onEdit"
        />
    </div>
</template>

<script>
    export default {
        data() {
            return {
                chosenAddressId: '1',
                add: '',
                edit: '',
                list: [
                    {
                        id: '1',
                        name: '',
                        tel: '13000000000',
                        address: ' 138  7  501 '
                    },
                    {
                        id: '2',
                        name: '',
                        tel: '1310000000',
                        address: ' 50 '
                    }
                ],
            }
        },
        methods: {
            onClickLeft(){
                this.$router.back(-1)
            },
            onAdd() {
                this.$router.push('/addressedit')
            },
            onEdit(item, index) {
            
            }
        }
    }
</script>

<style scoped>

</style>


